@import "../../global.scss";

.skillsContainer {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-top: 10px;
    

    @include mobile {
        
        padding-top: 7%;
    }

    h1 {
        @include mobile {
        height: 20%;
        font-size: 26px;
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        align-items: center;
        text-transform: uppercase;
        text-align: center;
        }
    }
    

    .skills {
        list-style: none;
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        align-items: center;
        justify-content: center;
        margin: 15px;
    

    .skillIcon {
        height: 150px;
        width: 170px;
        margin: 35px;
        // border: 2px solid purple;
        // background-color: #b4b0b8;
        // background: linear-gradient(135deg, #2d0052, #7a00de);
        object-fit: contain;
        padding: 15px;
        
        border-radius: 25px;
        color: #fff;
        // box-shadow: 0 0 2px #fff, 0 0 10px #fff, 0 0 20px #5c07ac, 0 0 30px #5c07ac,
        // 0 0 40px #2c035a, 0 0 50px #2c035a;
        
        // animation: shadow 1s infinite alternate;

        @include mobile {
        height: 25%;
        width: 40%;
        margin: 10px;
        padding: 0;
        border-radius: 10px;
        
        }
        
    }

    .skillIcon::before {
        // background-color: #5c07ac;
       
        content: "";
        position: absolute;
        width: 25px;
        height: 100%;
        inset: 0px;
        mix-blend-mode: screen;
    }
      
      @keyframes shadow {
        100% {
          box-shadow: 0 0 3px #fff, 0 0 10px #fff, 0 0 20px #fff, 0 0 40px #5c07ac,
            0 0 70px #2c035a, 0 0 80px #7320e0;
        }
      }
}
}


.skillssm {
    display: none;
    
    flex-direction: column;
    flex-wrap: wrap;
    align-items: center;
    

    @include mobile {
        display: flex;
        
        
    }

    .skills {
        list-style: none;
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        align-items: center;
        justify-content: center;

    .skillIconsm {
        height: 150px;
        width: 170px;
        border-radius: 15px;
        margin: 10px;
        
        
    }}
}